.CardBodyEdit {
  position: relative;
  width: 3%;
  text-align: center;
  align-items: center;
}

.card-body {
  align-items: center;
}

.CardIconCenter {
  margin: 0;
  position: absolute;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
.tdHarahCenter {
  text-align: center;
}
.dropDownCenter {
  text-align: center;
}
.dropdown-menu {
  cursor: hand;
}
.dropdown-item {
  cursor: pointer;
}
.btnHidden {
  display: none;
  margin-left: 10px;
  border-radius: 6px;
  background-color: transparent;
  outline: none;
  background-repeat: no-repeat;
  border: solid 2px green;
  border-radius: 30px;
}
.divBtnHide {
  display: flex;
  flex-direction: row;
}
.btnHidden i {
  color: darkgreen;
  font-size: 14px;
}
.selectCss {
  font-size: 18px;
  background-color: blue;
}
